tp-accordion {
  --margin-bottom: 8px;
  border-radius: 8px;
  background: hsl(var(--med-color-neutral-2));
}
tp-accordion:not(:last-child) {
  margin-bottom: var(--margin-bottom);
}
tp-accordion ion-item[slot=header] {
  --inner-padding-end: 0;
}
tp-accordion ion-item[slot=header] ion-label {
  font-size: 16px !important;
  font-weight: 600 !important;
  line-height: 24px !important;
}
tp-accordion div[slot=content] {
  padding: 16px;
}
tp-accordion.accordion-expanding .med-rotate, tp-accordion.accordion-expanded .med-rotate {
  --rotate: rotate(180deg);
}
tp-accordion[ds-name=secondary] {
  background: hsl(var(--med-color-neutral-3));
}
tp-accordion[ds-name=secondary] ion-item[slot=header] {
  --background: hsl(var(--med-color-neutral-3));
}

ion-alert.tp-hide {
  display: none !important;
}

