ion-button {
  --background: hsl(var(--med-color-brand-3));
  --background-activated: hsl(var(--med-color-brand-2));
  --background-focused: hsl(var(--med-color-brand-2));
  --background-hover: hsl(var(--med-color-brand-2));
  --background-activated-opacity: 1;
  --background-focused-opacity: 1;
  --background-hover-opacity: 1;
  --color: hsl(var(--med-color-neutral-10));
  --color-activated: hsl(var(--med-color-neutral-10));
  --color-focused: hsl(var(--med-color-neutral-10));
  --color-hover: hsl(var(--med-color-neutral-10));
  --border-radius: 500px;
  --box-shadow: none;
  --padding-top: 16px;
  --padding-bottom: 16px;
  --padding-start: 32px;
  --padding-end: 32px;
  height: 48px;
  font-weight: 600;
  font-size: 16px;
  line-height: 16px;
  text-transform: none;
  opacity: 1;
}
ion-button .med-icon {
  font-size: 24px;
  stroke: var(--color);
}
ion-button ion-icon[slot=start] {
  margin-right: 8px;
}
ion-button ion-icon[slot=end] {
  margin-left: 8px;
}
ion-button[disabled] {
  --background: hsl(var(--med-color-neutral-3));
  --color: hsl(var(--med-color-neutral-5)) !important;
  opacity: 1;
}
ion-button[disabled] ion-icon {
  stroke: var(--color);
}

ion-button.button {
  background-color: transparent !important;
  color: var(--color);
  opacity: 1 !important;
}

ion-button[ds-size=sm] {
  --padding-top: 12px;
  --padding-bottom: 12px;
  height: 40px;
}
ion-button[ds-size=sm] ion-icon {
  font-size: 24px;
}
ion-button[ds-size=xs] {
  --padding-top: 8px;
  --padding-bottom: 8px;
  --padding-start: 24px;
  --padding-end: 24px;
  height: 32px;
  font-size: 14px;
}
ion-button[ds-size=xs] ion-icon {
  font-size: 16px;
  height: 16px;
  width: 16px;
}
ion-button[ds-size=xs][icon-only] ion-icon {
  height: 24px;
  width: 24px;
}
ion-button[ds-size=xxs] {
  --padding-top: 6px;
  --padding-bottom: 6px;
  --padding-start: 16px;
  --padding-end: 16px;
  height: 24px;
  font-size: 12px;
  line-height: 12px;
}
ion-button[ds-size=xxs] ion-icon {
  font-size: 16px;
  height: 16px;
  width: 16px;
}

ion-button[icon-only] {
  --padding-top: 0;
  --padding-bottom: 0;
  --padding-start: 0;
  --padding-end: 0;
  width: 48px;
}
ion-button[icon-only][ds-size=sm] {
  --padding-top: 0;
  --padding-bottom: 0;
  --padding-start: 0;
  --padding-end: 0;
  width: 40px;
}
ion-button[icon-only][ds-size=xs] {
  --padding-top: 0;
  --padding-bottom: 0;
  --padding-start: 0;
  --padding-end: 0;
  width: 32px;
}
ion-button[icon-only][ds-size=xxs] {
  --padding-top: 0;
